	module.exports = "<h2 id=overview>Overview</h2> <p>A tooltip is used to provide contextual information for another component on hover. The tooltip itself is made up of two elements: arrow and content. One of the directional modifiers (<code>.pf-m-left</code>, <code>.pf-m-top</code>, etc.) is required on the tooltip component</p> <h2 id=accessibility>Accessibility</h2> <table> <thead> <tr> <th>Attribute</th> <th>Applied To</th> <th>Outcome</th> </tr> </thead> <tbody> <tr> <td><code>aria-describedby=&quot;[id value of applicable content]&quot;</code></td> <td><code>.pf-c-tooltip</code></td> <td>Gives the tooltip an accessible description by referring to the tooltip content that describes the primary message or purpose of the dialog. Not used if there is no static text that describes the popover.</td> </tr> </tbody> </table> <h2 id=usage>Usage</h2> <table> <thead> <tr> <th>Class</th> <th>Applied To</th> <th>Outcome</th> </tr> </thead> <tbody> <tr> <td><code>.pf-c-tooltip</code></td> <td><code>&lt;div&gt;</code></td> <td>Creates a tooltip.</td> </tr> <tr> <td><code>.pf-c-tooltip__arrow</code></td> <td><code>.pf-c-tooltip</code></td> <td>Creates an arrow pointing towards the element the tooltip describes. <strong> Required.</strong></td> </tr> <tr> <td><code>.pf-c-tooltip__content</code></td> <td><code>.pf-c-tooltip</code></td> <td>Creates the body of the tooltip. <strong> Required.</strong></td> </tr> <tr> <td><code>.pf-m-left</code></td> <td><code>.pf-c-tooltip</code></td> <td>Puts arrow to the left of the tooltip&#39;s content.</td> </tr> <tr> <td><code>.pf-m-right</code></td> <td><code>.pf-c-tooltip</code></td> <td>Puts arrow to the right of the tooltip&#39;s content.</td> </tr> <tr> <td><code>.pf-m-top</code></td> <td><code>.pf-c-tooltip</code></td> <td>Puts arrow on top of the tooltip&#39;s content.</td> </tr> <tr> <td><code>.pf-m-bottom</code></td> <td><code>.pf-c-tooltip</code></td> <td>Puts arrow on the bottom of the tooltip&#39;s content.</td> </tr> </tbody> </table> ";

